@import "mixin/mixins";
@import "mixin/normal";

main {
	padding-bottom: 48px; }

.banner {
	background: url(../img/bunner-categories.png);
	background-size: cover;
	height: 200px;
	padding: 65px 0;
	.banner_title {
		color: white;
		font-family: PlayfairDisplay;
		font-size: 50px;
		font-weight: 700;
		line-height: 60px;
		text-align: center;
		transition: 0.3s; }
	.bunner_number {
		color: white;
		font-family: OpenSans;
		font-size: 14px;
		font-weight: 400;
		line-height: 30px;
		text-align: center; } }

.breadcrumb {
	margin: 30px 0;
	li,
	a {
		color: #555;
		font-size: 12px;
		font-weight: 400;
		line-height: 15px; } }

.breadcrumb>li+li:before {
	font-size: 4px;
	color: #bdbdbd;
	font-family: FontAwesome;
	content: '\f111';
	padding: 0 15px;
	vertical-align: middle; }


.sort {
	background: #fff;
	padding: 25px 30px;
	margin-bottom: 25px;
	color: #757575;
	font-size: 14px;
	font-weight: 400;
	position: relative;
	.sort_text {
		margin-right: 30px; }
	.sort_item {
		position: relative;
		display: inline-block;
		margin-right: 50px;
		&::after {
			content: '\f107';
			font-family: FontAwesome;
			position: absolute;
			top: 2px;
			right: -15px; } }
	.sort_item.active {
		color: #734ea4; } }
.state_buttons {
	display: inline-block;
	position: absolute;
	right: 35px;
	.item_vertical {
		height: 23px;
		width: 23px;
		margin-right: 15px; }
	.item_horizontal {
		height: 23px;
		width: 28px; } }


.products {
	.content {
		background: #fff; } }


.horizontal_items {
	.h_item {
		margin-bottom: 20px; }
	.col-content {
		background: #fff;
		display: block; }
	.product_item {
		margin: 0;
		padding: 15px 0; }
	.product_picture {
		height: 178px;
		img {
			border-radius: 5px; } }
	.subcol_content {
		position: relative;
		height: 170px;
		// margin-left: -15px
		// margin-right: -15px
		&>* {
			text-align: left; } }
	.category_name {
		margin-top: 7px; }
	.product_name {
		height: 82px;
		margin: 0; }
	.price_holder {
		position: absolute;
		bottom: 7px; }
	.item_rating {
		margin: 0;
		margin-top: 5px; }
	.like_ikon {
		right: 10px;
		top: 5px; }
	.shipping,
	.feedback,
	.orders {
		color: #111;
		font-size: 12px;
		font-weight: 400;
		margin-top: 15px; }
	span.shipping_state {
		font-size: 14px;
		font-weight: 600; }
	a {
		display: block; }
	.hide_now {
		display: none; } }

.pagination {
	background: #fff;
	width: 100%;
	font-size: 15px;
	font-weight: 600;
	text-align: center;
	padding: 12px 0;
	li {
		a {
			border: none;
			color: #888;
			text-transform: uppercase;
			padding: 8px 15px;
			margin: 0 5px;
			float: none;
			border-radius: 50%;
			&:hover {
				background: #fff;
				color: #ffbf35; } } }

	li.active {
		a {
			background: #ffbf35;
			box-shadow: 0 1px 2px rgba(0, 0, 0, 0.16);
			color: #fff;
			&:hover {
				background: #ffbf35; } } }



	.fa-long-arrow-left,
	.fa-long-arrow-right {
		padding: 0 15px; }




 }	///////filters///////

.left_side_bar {
	.content {
		padding: 24px;
		background: #fff; }

	.filter_title {
		color: #222;
		font-size: 16px;
		font-weight: 600;
		margin: 0;
		margin-bottom: 20px;
		text-transform: uppercase;
		&:hover {
			cursor: pointer; }
		&:focus {
			outline: none; } } }

.filter_content {
	font-size: 14px;
	color: #555;
	font-weight: 400; }
.check_box {
	margin-bottom: 50px;
	.check_box_item {
		padding: 8px 0; } }
.check_box_item {
	input[type=checkbox]:checked + label:after {
		content: "";
		width: 25px;
		height: 25px;
		border-radius: 2px;
		position: absolute;
		left: 0px;
		top: 0px;
		border: 1px solid $purple;
		background: #fff;
		opacity: 1; }
	input[type=checkbox]:checked + label:before {
		content: '\f00c';
		font-family: "FontAwesome";
		width: 25px;
		height: 25px;
		display: block;
		margin: auto;
		border-radius: 2px;
		background: $purple;
		color: $white;
		line-height: 25px;
		position: absolute;
		top: 0px;
		left: 0px;
		z-index: 15;
		text-align: center; }

	.filter_check_label {
		&:before {
			content: "";
			width: 25px;
			height: 25px;
			border-radius: 2px;
			position: absolute;
			left: 0px;
			top: 0px;
			background: #f8f8f8;
			@include transition(0.2s); }
		&:after {
			content: '';
			font-family: "FontAwesome";
			width: 25px;
			height: 25px;
			opacity: 0;
			display: block;
			margin: auto;
			border-radius: 2px;
			background: $purple;
			color: $purple;
			line-height: 25px;
			position: absolute;
			top: 0px;
			left: 0px;
			z-index: 10;
			@include transition(0.2s); } }
	input[type="checkbox"] {
		display: none; }
	label {
		padding-left: 40px;
		position: relative;
		cursor: pointer;
		font-weight: normal;
		line-height: 25px;
		margin: 0;
		margin-bottom: 20px;
		@include transition(0.2s);
		&:hover {
			color: $purple;
			&:after {
				opacity: 1; } } } }

.price_range {
	text-align: justify;
	&:after {
		content: '';
		display: inline-block;
		width: 100%; }
	#slider-range {
		height: 6px;
		width: 100%;
		background: #ebebeb;
		border-radius: 3px;
		margin: auto;
 }		// margin-bottom: 10px
	#min-amount, #max-amount {
		width: 44%;
		height: 30px;
		background-color: #f8f8f8;
		border-radius: 5px;
		border: none;
		margin-top: 25px;
		// margin-bottom: 50px
		text-align: center; }
	#min-amount {
		margin-right: 4%; }
	#max-amount {
		margin-left: 4%; } }


.color_choice {
	input[type="checkbox"] {
		display: none; }
	label {
		position: relative;
		cursor: pointer;
		font-weight: normal;
		line-height: 25px;
		margin-right: 35px;
		margin-bottom: 50px;
		&:before {
			content: '';
			display: block;
			width: 25px;
			height: 25px;
			border-radius: 50%;
			position: absolute;
			top: 0;
			left: 0; } }
	.black:before {
		background-color: #363a5b; }
	.red:before {
		background-color: #ff4a5d; }
	.yellow:before {
		background-color: #ffbf35; }
	.green:before {
		background-color: #cde423; }
	.blue:before {
		background-color: #1cc9e4; }
	input[type="checkbox"]:checked + label:after {
		content: '\f00c';
		font-family: "FontAwesome";
		font-size: 9px;
		line-height: 25px;
		color: white;
		display: block;
		position: absolute;
		left: 8px;
		z-index: 100; } }

.brand_choice {
	#brand_search {
		color: #888;
		font-family: 'Karla', sans-serif;
		font-size: 13px;
		font-weight: 400;
		width: 100%;
		height: 30px;
		background-color: #f8f8f8;
		border-radius: 5px;
		border: none;
		padding-left: 30px; }
	.label_search {
		position: relative;
		margin-bottom: 20px;
		&:after {
			content: '\f002';
			font-family: "FontAwesome";
			color: #888;
			font-size: 13px;
			display: block;
			position: absolute;
			left: 10px;
			top: 0;
			line-height: 30px;
			z-index: 100; } } }

.brands_group {
	max-height: 275px;
	overflow: auto; }


.ui-icon {
	float: right; }

.ui-icon,
.ui-state-focus .ui-icon,
.ui-state-hover .ui-icon,
.ui-button:hover .ui-icon {
	background: url('../img/Arrow-Down icon.png') no-repeat; }
.ui-state-active .ui-icon,
.ui-button:focus .ui-icon {
	background: url('../img/Arrow-Up icon.png') no-repeat; }

.ui-slider {
	.ui-slider-handle {
		height: 20px;
		width: 20px;
		border-radius: 50%;
		border: 6px solid #f0f0f0;
		background: transparent;
		&:focus {
			outline: none; } }
	.ui-widget-header {
		background: #ffbf35; } }
.ui-slider-horizontal {
	.ui-slider-handle {
		left: -50%;
		margin-left: -5px;
		&+.ui-slider-handle {
			margin-left: -15px; } } }







