* {
	box-sizing: border-box; }
html {
	width: 100%;
	height: 100%; }
body {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	min-width: 320px; }
a,
a:hover {
	text-decoration: none;
	color: inherit;
	pointer: cursor; }
a:focus,
a:active {
	outline: 0; }


.dropdown-menu {
	left: auto;
	margin-top: 0;
	border-radius: 0px;
	border: none;
	box-shadow: none; }

.navbar-toggle .icon-bar {
	background: $font_grey; }

.navbar-toggle {
	margin: 4px 0; }
.navbar-toggle:hover {
	border: 1px solid transparent;
	.icon-bar {
		background: #673ab7; } }
.slick-slide {
	&:focus {
		outline: none; } }

input {
	&:focus {
		outline: none; } }
button,
button:hover,
button:active {
	background: none;
	border: none;
	outline: none; }

.nav-tabs>li.active>a,
.nav-tabs>li.active>a:focus,
.nav-tabs>li.active>a:hover,
.nav-tabs>li>a:hover,
.nav-tabs>li>a {
	border: none; }

.nav>li>a:focus,
.nav>li>a:hover {
	background: none; }

.pagination>li>a:focus,
.pagination>li>a:hover,
.pagination>li>span:focus,
.pagination>li>span:hover {
	z-index: 2;
	background-color: #fff;
	border: none; }

.pagination>.active>a,
.pagination>.active>a:focus,
.pagination>.active>a:hover,
.pagination>.active>span,
.pagination>.active>span:focus,
.pagination>.active>span:hover {
	z-index: 3;
	color: #fff;
	cursor: default;
	background-color: #ffbf35;
	border: none; }

.hide_now {
	display: none; }


// .ui-accordion
#accordion {
	.ui-accordion-header {
		background: none;
		padding: 0;
		border: none; }
	.ui-widget-content {
		border: none;
		padding: 10px 10px 0;
		margin-left: -10px;
		margin-right: -10px;
		margin-bottom: 40px;
		// padding-bottom: 40px
 } }		// overflow: hidden
*:focus {
	outline: none; }


